import { getSettingOptionValue } from '../settings/index';

let wakeLock = null;
let wakeLockRequested = false;
let isBeingKeptAwake = false;

export async function askForWakeLockPermission(): Promise<boolean> {
  if (!wakeLockRequested && getSettingOptionValue('wake_lock')) {
    wakeLockRequested = true;
    try {
      if ('wakeLock' in navigator) {
        wakeLock = await navigator.wakeLock.request('screen');
        wakeLock.addEventListener('release', () => {
          isBeingKeptAwake = false;
        });
        document.addEventListener('visibilitychange', async () => {
          if (wakeLock !== null && document.visibilityState === 'visible') {
            try {
              wakeLock = await navigator.wakeLock.request('screen');
              isBeingKeptAwake = true;
            } catch (err) {
              isBeingKeptAwake = false;
            }
          }
        });
        isBeingKeptAwake = true;
      }
    } catch (e) {
      return false;
    }
    return false;
  }
}

export async function getWakeLockStatus(): Promise<boolean> {
  if (wakeLockRequested) {
    return isBeingKeptAwake;
  } else {
    return false;
  }
}
